<template>
  <div>
    <Modal
      v-model="modal"
      title="新建客户" :styles="{width:'100%'}" class-name="modal-box">

      <div class="master-content">
        <div class="row">
          <label>公司名称:</label>
          <Input v-model="customName" type="text" name="customName" placeholder=""
                 @on-blur='userDataNum' @on-focus='onFocusName' style="width: 300px"></Input>
          <span :class="{'label-right-active':isAtive}" class="label-right">{{soltNameLen}}</span>
        </div>
        <div class="row"><label>说明:</label>

          <Input v-model="customDesc" type="textarea" :rows="10" placeholder="选填"
                 @on-blur='checkDesc' @on-focus='focusDesc' style="width:300px;"></Input>
          <span :class="{'label-right-active':invalidDesc}" class="label-right">{{invalidDescTip}}</span>

          <!--<textarea v-model="customDesc" placeholder="选填" style="width:300px;height:200px;"></textarea>-->

        </div>

      </div>
      <div slot="footer">
        <Button type="text" @click="cancel">取消</Button>
        <Button class="btnColor" type="primary" @click="createNewCustom">新建客户</Button>
      </div>
    </Modal>
  </div>
</template>
<script>

  export default {
    data() {
      return {
        customName: "",
        customDesc: "",
        isAtive: false,
        soltNameLen: '最多可输入100个字符',
        invalidDesc: false,
        invalidDescTip: '最多可输入100个字符',
        modal: false
      }


    },
    methods: {
      beforeLoad: function () {
        this.customName = "";
        this.customDesc = "";
        this.soltNameLen = "最多可输入100个字符";
        this.isAtive = false;
        this.invalidDesc = false;
        this.invalidDescTip = '最多可输入100个字符';
        this.modal = true;

      },
      cancel() {
        this.soltNameLen = "最多可输入100个字符";
        this.isAtive = false;
        this.invalidDesc = false;
        this.invalidDescTip = '最多可输入100个字符';
        this.modal = false;
      },
      createNewCustom: function () {
        if (this.invalidDesc || this.isAtive) {
          return false;
        }
        let url = "/custom/createCustom.json?customName=" + encodeURIComponent(this.customName)
          + "&customDesc=" + encodeURIComponent(this.customDesc);
        this.$Http.get(url, {}).then(res => {
          if (res.body.status == 0) {
            this.modal = false;
            this.$emit("filterParent");
          }
        });

      },
      onFocusName() {
        this.soltNameLen = "最多可输入100个字符";
        this.isAtive = false;

      },
      focusDesc() {
        this.invalidDesc = false;
        this.invalidDescTip = '最多可输入100个字符';
      },
      userDataNum() {
        //姓名位数不在0-100间
        if (this.customName != "") {
          var reg = /^[\s\S]{1,100}$/;
          var r = this.customName.match(reg);
          if (r == null) {
            this.soltNameLen = "长度超过100个字符"
            this.isAtive = true;
          }
        } else {
          this.soltNameLen = "公司名称不能为空"
          this.isAtive = true;
        }
      },
      checkDesc() {
        if (this.customDesc != "") {
          var reg = /^[\s\S]{0,100}$/;
          var r = this.customDesc.match(reg);
          if (r == null) {
            this.invalidDescTip = "长度超过100个字符"
            this.invalidDesc = true;
          }
        }
      }


    }

  }

</script>

<style scoped>
  .master-content {
    background: #fff;
    height: 100%;
    padding: 10px;
  }

  .master-content .row {
    padding: 10px;
    display: -webkit-flex; /* Safari */
    display: flex;
    align-items: center;

  }

  .master-content .row label {
    width: 100px;
  }

  .master-content .row.down {
    border-top: 1px solid #eaeaea;
    height: 100px;

  }

  .label-right-active {
    color: red !important;
  }

  .label-right {
    display: inline-block;
    margin-left: 20px;
  }

</style>



