<!-- 客户管理 -->
<template>


  <div>

    <top-title title="客户管理" isShowClose="false">
    </top-title>
    <div class="master-content">
      <div class="master-content-nav">
        <div class="l">
          <Button class="btnColor" icon="plus" type="primary" @click="createNewCustom">新建公司</Button>
          <Button class="btnStart" type="primary" :disabled="isAdds" @click="addAll">启用</Button>
          <Button class="btnCancel" type="primary" :disabled="isDeletes" @click="deleteAll">删除</Button>
          <Button type="text" v-on:click="trigger">筛选&nbsp;&nbsp;<Icon type="arrow-down-b"></Icon>
          </Button>
        </div>
        <div class="r">
          <Input v-model="customName" placeholder="请输入公司名称或说明" style="width: 300px">
          <!-- <Icon type="search" slot="append" @click.native="getFilterData"></Icon> -->
          </Input>
          <Button class="btnColor" type="primary" icon="search" @click="getFilterData">搜索</Button>

        </div>


      </div>
      <div class="master-content-middle" v-show="isShow">
        <div class="label">
          状态：
        </div>
        <div class="label-value" id="filterForm">
          <Select v-model="customStatus" style="width:200px" @on-change="getFilterData">
            <Option value="-99">全部</Option>
            <Option value="1">启用</Option>
            <Option value="0">删除</Option>
          </Select>
        </div>

      </div>
      <div class="master-content-content" style="margin-top: 10px;">
        <Table border ref="selection" :columns="customColumns" :loading="loading" :data="customData"
               @on-selection-change="selectData"></Table>
        <Page :total="totalCount" :current="pageNo" show-sizer show-total
              @on-change="changePageNo" @on-page-size-change="changePageSize"></Page>

      </div>


    </div>

    <new-customer ref="newCustomer" v-on:filterParent="getFilterData"></new-customer>
    <update-customer ref="updateCustomer" v-on:filterParent="getFilterData"></update-customer>

  </div>
</template>
<script>
  import PageSize from '@/components/common/PageSize'
  import topTitle from '@/components/common/topTitle'
  import updateCustomer from '@/components/pages/deliveryManagement/customerManagement/child/updateCustomer'
  import newCustomer from '@/components/pages/deliveryManagement/customerManagement/child/newCustomer'

  export default {
    data() {
      return {
        isShow: false,
        customName: "",
        customStatus: "-99",
        isAdds: true,
        isDeletes: true,
        addCustomParams: [],
        deleteCustomParams: [],
        totalCount: 0,
        pageNo: 1,
        pageSize: 10,
        loading: true,
        customColumns: [
          {
            type: 'selection',
            width: 60,
            align: 'center'

          },
          {
            title: '公司名称',
            key: 'customName'
          },
          {
            title: '状态',
            key: 'customStatus'
          },
          {
            title: '说明',
            key: 'customDesc'
          },
          {
            title: '操作',
            key: 'customAction',
            width: 150,
            align: 'center',
            render: (h, params) => {
              console.log(params);
              var customStatusNum = params.row.customStatusNum;
              if (customStatusNum == 0) {
                return;
              }
              return h('div', [
                h('Button', {
                  props: {
                    type: 'primary',
                    size: 'small'
                  },
                  style: {
                    marginRight: '5px',
                    backgroundColor: '#1fa2fd',
                    borderColor: '#1fa2fd'
                  },
                  on: {
                    click: () => {
                      // this.$router.push({path:"/updateCustomer",query:{"customId":params.row.customId}});
                      this.$refs.updateCustomer.beforeLoad();
                      this.$refs.updateCustomer.getCustom(params.row.customId);

                      // this.show(params.index)
                    }
                  }
                }, '修改'),
                h('Button', {
                  props: {
                    type: 'error',
                    size: 'small'
                  },
                  style: {

                    backgroundColor: '#ea0b00',
                    borderColor: '#ea0b00'
                  },

                  on: {
                    click: () => {
                      //this.remove(params.index)

                      let v = [];
                      v.push(params.row);

                      this.deleteBox(v);
                    }
                  }
                }, '删除')
              ]);
            }
          }
        ],
        customData: []

      }
    },
    created() {
      //初始化数据
      this.getCustomData();

    },
    methods: {
      selectData: function (selection) {

        let canDeletes = selection.filter((item) => {
          return item.customStatusNum == 1;
        });

        let canAdds = selection.filter((item) => {
          return item.customStatusNum == 0;
        });

        this.isAdds = !canAdds.length > 0;

        this.addCustomParams = canAdds;

        this.isDeletes = !canDeletes.length > 0;
        this.deleteCustomParams = canDeletes;


      },
      addAll: function (customIds) {
        this.addBox(this.addCustomParams);

      },
      deleteAll: function () {
        this.deleteBox(this.deleteCustomParams);

      },
      createNewCustom: function () {
        this.$refs.newCustomer.beforeLoad();
      },
      trigger: function () {

        this.isShow = !this.isShow;
      },
      addBox(params) {
        console.log(params);
        let customNames = params.map((item, index) => {
          return item.customName;
        });
        let customIds = params.map((item, index) => {
          return item.customId;
        });
        this.$Modal.confirm({
          title: '启用公司',
          content: `你确定要启用` + customNames.join(","),
          onOk: () => {

            this.addCustomData(customIds.join(","));
          }
        })
      },


      deleteBox(params) {
        console.log(params);
        let customNames = params.map((item, index) => {
          return item.customName;
        });
        let customIds = params.map((item, index) => {
          return item.customId;
        });
        this.$Modal.confirm({
          title: '删除公司',
          content: `你确定要删除` + customNames.join(","),

          onOk: () => {
            this.deleteCustomData(customIds.join(","));
            console.log(customIds.join(","))
          }
        })
      },
      addCustomData: function (customIds) {
        let url = "/custom/enableCustom.json?customId=" + customIds;
        this.$Http.get(url, {}).then(res => {
          this.$Message.info('启用公司成功!');

          this.getCustomData();
        });


      },

      deleteCustomData: function (customIds) {
        let url = "/custom/deleteCustom.json?customId=" + customIds;
        this.$Http.get(url, {}).then(res => {
          this.$Message.info('删除公司成功!');

          this.getCustomData();
        });
      },

      getCustomData: function () {
        this.isAdds = true;
        this.isDeletes = true;
        this.addCustomParams = [];
        this.deleteCustomParams = [];

        let nameLike = this.customName;

        let status = this.customStatus;
        let url = "/custom/getCustomList.json?nameLike=" + encodeURIComponent(nameLike)
          + "&status=" + status + "&pageNo=" + this.pageNo + "&pageSize=" + this.pageSize;
        this.$Http.get(url, {}).then(res => {
          if (res.body.status === 0) {
            this.loading = false;
            let result = res.body.data.result;
            this.customData = [];
            for (let value of result) {
              let j = {};
              j["customName"] = value["customName"];
              j["customStatusNum"] = value["customStatus"];
              j["customStatus"] = value["customStatus"] == 0 ? "删除" : "启用";
              j["customDesc"] = value["customDesc"];
              j["customId"] = value["fxAdCustomId"];
              this.customData.push(j);
            }
            this.totalCount = res.body.data.totalCount;
          }


        });

      },
      changePageNo: function (pageNo) {
        this.loading = true;
        // this.pageSize = 10;
        this.pageNo = pageNo;
        this.getCustomData();
      },
      changePageSize: function (pageSize) {

        this.pageNo = 1;
        this.pageSize = pageSize;
        this.getCustomData();
      },
      getFilterData: function () {
        this.loading = true;
        // this.pageSize = 10;
        // this.pageNo = 1;
        this.getCustomData();
      }

    },
    components: {topTitle, newCustomer, updateCustomer}

  }
</script>

<style>
  @import '../../common.css';

  .ivu-page {
    text-align: right;
    margin-right: 30px;
    padding: 10px 10px;
  }

  .master-content {
    background: #fff;
    height: 100%;
    padding: 10px;
  }

  .master-content-nav {
    display: -webkit-flex; /* Safari */
    display: flex;
    justify-content: space-between;
    padding: 10px 0px;
  }

  .master-content-middle {
    display: -webkit-flex; /* Safari */
    display: flex;
    padding: 30px 10px;
    border: 1px solid #d7f0f2;
    align-items: center;
  }

  .master-content-middle .label {
    padding: 0px 20px;
  }
</style>



